@media (max-width: 895px){
  .home-image-section{
    max-width: 400px;
    max-height: 350px;
  }
  .home-text-section .heading-home{
    font-size: 3rem;
  }
  .home-text-section .heading-home span{
    font-size: 4rem;
  }
  .home-text-section p{
    font-size: 14px;
  }
  .btn{
    margin-top: 25px;
    height: 4rem;
    width: 160px;
    align-content: center;
  }
  .info-btn{
    align-content: center;
    font-size: 2rem;
  }
  .heading-home,
  .primary-text,
  .btn {
    text-align: center;
    align-self: center;
  }
}
  @media (max-width: 600px){
    .home-text-section .heading-home{
      font-size: 3rem;
      text-align: center;
    }
    .home-text-section .heading-home span{
      font-size: 3.3rem;
    }
    .home-text-section .primary-text{
      margin-top: 20px;
    }
    .btn{
      margin-top: 50px;
    }
  }